/*  "header header header"
    "div1 div1 div1"
    "div2 div2 div2"
    "div3 div3 div3"
    "footer footer footer"; 
    */
.header {
  grid-area: header;
  display: grid;
  width: 100%;
  grid-template-areas: "logo logo nav";
}
.header__logo {
  grid-area: logo;
}
.header__logo img {
  max-width: 100%;
  height: auto;
  padding: 0.9375rem;
}
.header__nav {
  grid-area: nav;
}
.header__nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__nav ul li {
  padding: 0.9375rem;
}
.header__nav ul li a {
  color: red;
}

.first {
  grid-area: div1;
  width: 100%;
}
.first .card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
}
.first .card__image img {
  max-width: 100%;
  height: auto;
}
.first .card__heading {
  padding: 20px;
}
.first .card__explain {
  grid-area: explain;
  width: 100%;
}
.first .card__getstarted {
  grid-area: form;
  width: 100%;
}

.second {
  grid-area: div2;
}

.third {
  grid-area: div3;
  background: var(--DesaturatedBlue);
}
.footer {
  grid-area: footer;
  background: var(--VeryDarkBlue);
  width: 100%;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-open-sans);
  display: grid;
  grid-template-areas: "header header header" "div1 div1 div1" "div2 div2 div2" "div3 div3 div3" "footer footer footer";
}

:root {
  --VeryDarkBlue: hsl(243, 87%, 12%);
  --DesaturatedBlue: hsl(238, 22%, 44%);
  --BrightBlue: hsl(224, 93%, 58%);
  --ModerateCyan: hsl(170, 45%, 43%);
  --LightGrayishBlue: hsl(240, 75%, 98%);
  --LightGray: hsl(0, 0%, 75%);
}

:root {
  --font-raleway: "Raleway", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

.container {
  padding: 0;
  max-width: 69.375rem;
  margin: 0;
}
@media (min-width: 71.25em) {
  .container {
    padding: 0;
  }
}

h1, h2, h3 {
  font-family: var(--font-raleway);
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  text-align: center;
  color: var(--VeryDarkBlue);
  font-weight: 700;
}

a, a:visited, a:active {
  text-decoration: none;
}

ul {
  list-style-type: none;
}